import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import CssBaseline from "@material-ui/core/CssBaseline"
import "../../App.css";
import { BrowserRouter,Route,Link } from "react-router-dom"
import Home from "../pages/Home/Home"
import Profile from "../pages/Profile/Profile"


const useStyles = makeStyles((theme) => ({
  div: {
    position: "fixed",
    zIndex:1000,
    width: "100%",
    margin: 0,
    backgroundColor: "rgba(0,0,0,0.4)",
    minHeight: "auto",
  },
  title: {
    flexGrow: 1,
    color: "white",
  },
  logo : {
    cursor: "pointer",
    color:"#8EC5FC",  
    padding: 5,
    "&:hover": {
      color: "rgba(255,255,255,0.7)",
    },
    "&:visited": {
      borderBottom: "1px solid white",
    }
  },
  profile: {
    cursor: "pointer",
    color: "#FFDEE9",
    float: "right",
    "&:hover": {
      color: "rgba(255,255,255,0.7)",
    },
  }
}));

function Header() {
  const classes = useStyles();

  return (
    <BrowserRouter>
    <div className={classes.div}>
      <CssBaseline />
        <Toolbar className={classes.toolbar}>
          <Typography variant="h6" className={classes.title}>
              <Link to="/Weather-SPA-App/" className={classes.logo}>HOME</Link>
              <Link to="/Weather-SPA-App/PROFILE" className={classes.profile}>PROFILE</Link>
          </Typography>
        </Toolbar>
    </div>
    <Route exact path="/Weather-SPA-App/" ><Home /></Route>
    <Route path="/Weather-SPA-App/PROFILE"><Profile /></Route>
    </BrowserRouter>
  );
}
export default Header;