import React from 'react'
import Icon from "./Image/TwitterIcon.PNG"
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import "../../../App.css"

export default function Profile() {
    return (
            <div className="profile-container">
                <h2>PR<AccountCircleIcon className="profileicon"/>FILE</h2>
                <div className="profile-context">
                    <img className="twittericon" src={Icon}></img>
                    <p>SHUJI'S PORTFOLIOへようこそ。2000年生まれの20歳です。<br />
                    現在は独学で2020年9月から、フロントエンジニアとしての転職を目指し、<br/>
                    HTML,CSS,JavaScript,React,Pythonを独学で勉強しております。<br />
                    前職ではSESの会社ではCCNA,LPICの学習を行いました。</p>
                </div>
            </div>
    )
}
