import React, {useState} from 'react'

export default function Form(props) {
  //useState を呼び出して state 変数（val）を宣言して初期値を設定
  const [val, setVal] = useState('');
  // onChange イベントのハンドラ
  function handleChange(event) {
    // ユーザがタイプする度に setVal で state の値 val を入力値で更新 
    setVal(event.target.value);
  }
 
  // onSubmit イベントのハンドラ
  function handleSubmit(event) {
    // 送信ボタンがクリックされたらその時点での state の値 val をコンソールに出力
    getWether();
    event.preventDefault();
  }
      //現在の天気を取得する場所の名前
      function getWether(){
      let targetCityName = val;
      let appId = "9b7ab6a64645f6c6b2aa462f817e4083";

      const requestUrl = "https://api.openweathermap.org/data/2.5/weather?APPID=" + appId + "&lang=ja&units=metric&q=" + targetCityName + ",jp;";
    //Ajax通信用のオブジェクトを作成
      let xhr =new XMLHttpRequest();
  
      //通信方式とURLを設定
      xhr.open("GET",requestUrl);
  
      //通信を実行する
      xhr.send();
  
      //通信ステータスが変わったら実行される関数
      xhr.onreadystatechange = function(){
          //通信が完了
          if(xhr.readyState === 4 && xhr.status === 200){
              ShowTodaysWeather(xhr.responseText);
          }
      }
      }
  

      /**
       * 今日の天気を表示する
       */
      function ShowTodaysWeather(response){

        let obj = JSON.parse(response);

        let weather = obj.weather[0].description;
        let city = obj.name;
        let temp = obj.main.temp;
        
        if(weather === "厚い雲" || weather === "雲" || weather=== "曇りがち" || weather === "薄い雲")　{
            weather = "曇り";
        }
        if(val) { 
        // p要素を追加
        const newElement = document.createElement("p")
        // 親要素
        const parentDiv = document.getElementById("parent-div");
        // テキストを追加
        const newContent = document.createTextNode("現在の"+city+"の天気は"+weather+"で、"+"気温は"+temp+"度です。" );
        // p要素にテキストを追加
        newElement.appendChild(newContent); 
        // 親要素の最初に追加
        parentDiv.insertBefore(newElement,parentDiv.firstChild);
        }  else return;
      }

      function removeText() {
        const parentDiv = document.getElementById("parent-div");
        if(parentDiv.hasChildNodes()){
          parentDiv.removeChild(parentDiv.firstChild)
        }else return;
      }
 

  return (
      <div style={{paddingTop: "5vh",textAlign: "center"}}>
        <form onSubmit={handleSubmit}>
        <label >
        {props.name}: 
            <input type="text" value={val} onChange={handleChange} style={{height: "5vh",outline: "none"}} placeholder="例: 大阪市"/>
            <input type="submit" value="調べる" style={{height: "5vh",cursor: "pointer"}}/>
        </label>
        </form>
        <button onClick={removeText}>天気予報を削除する</button>
        <div id="parent-div">
        </div>
      </div>
  );
}

